import { Link as RouterLink } from 'react-router-dom';
// @mui
import { Link, Typography, Stack } from '@mui/material';
// routes
import { PATH_AUTH } from '../../routes/paths';
// layouts
import AuthLayout from '../../layouts/AuthLayout';
// hooks
import useAuth from '../../hooks/useAuth';
// components
import Page from '../../components/Page';
// sections
import { RegisterForm } from '../../sections/auth/register';

// ----------------------------------------------------------------------

import { APP_NAME } from '../../config';

// ----------------------------------------------------------------------

export default function Register() {
  const { setting } = useAuth();
  const appName = setting?.general?.app_name || APP_NAME;

  return (
    <Page title="Register">
      <AuthLayout title="Get Started">
        <Stack spacing={2} sx={{ mb: 5, position: 'relative' }}>
          <Typography variant="h4">Get started with {appName}.</Typography>

          <Typography sx={{ color: 'text.secondary' }}>Create your account to continue.</Typography>
        </Stack>

        <RegisterForm />

        <Typography variant="body2" align="center" sx={{ color: 'text.secondary', mt: 3 }}>
          By registering, I agree to {appName}&nbsp;
          <Link underline="always" color="text.primary" component={RouterLink} to={PATH_AUTH.termsOfService}>
            Terms of Service
          </Link>
          &nbsp;and&nbsp;
          <Link underline="always" color="text.primary" component={RouterLink} to={PATH_AUTH.termsOfService}>
            Privacy Policy
          </Link>
          .
        </Typography>

        <Typography variant="body2" sx={{ mt: 3, textAlign: 'center' }}>
          Already have an account?{' '}
          <Link variant="subtitle2" to={PATH_AUTH.login} component={RouterLink} sx={{ color: 'primary.main' }}>
            Login
          </Link>
        </Typography>
      </AuthLayout>
    </Page>
  );
}
